#particles-js {
  position: absolute;
  top: 0px;
  left: 0px;
  height: 100%;
  width: 100%;
  z-index: 1;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.main-header {
  position: relative
}

.main-container {
  width: 100%;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  padding-top: 100px;
  padding-left: 40px;
  padding-right: 40px;
  z-index: 10;
  position: relative
}

.left-container {
  -ms-flex: 1 1;
      flex: 1 1;
  height: 100%;
  margin: 0px;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 10px;
  margin-right: 0px;
}

.right-container {
  margin: 0px;
  width: 250px;
  height: 100%;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-direction: column;
      flex-direction: column;
  margin: 10px;
}

.qrcode-container {
  width: 100%;
  height: 100%;
  text-align: center
}

.qrcode {
  margin: 10px auto;
}

.card-content {
  min-height: 320px
}

/*==================================================
 * Shadow 1
 * ===============================================*/
 .shadow1 {
  -webkit-box-shadow: 0 10px 6px -6px #777;
          box-shadow: 0 10px 6px -6px #777;
}


